import React, { useContext, useEffect, useState } from "react";
import Modal from "react-modal";
import { GrFormClose } from "react-icons/gr";

const customStyles = {
    content: {
        top: "30%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
        background: "transparent",
        border: "none",
    },
};

function MessageModal(props) {
    // Modal.setAppElement('#yourAppElement');
    const [modalIsOpen, setIsOpen] = React.useState(false);


    function afterOpenModal() { }

    function closeModal() {
        setIsOpen(false);

    }

    useEffect(() => {
        props?.message && setIsOpen(true)
    }, [props?.message])




    return (
        <div className="">

            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
                shouldCloseOnOverlayClick={false} // disable outside click close
            >
                <div className="p-10 py-12 border border-indigo-300 bg-indigo-300">
                    <p onClick={closeModal} className="absolute right-8 top-6 cursor-pointer capitalize text-white font-light hover:underline">
                        close
                    </p>

                    <div>
                        {props?.message}
                    </div>


                </div>

            </Modal>
        </div>
    );
}

// ReactDOM.render(<App />, appElement);

export default MessageModal;
