import React, { useContext, useEffect, useState } from "react";
import Modal from "react-modal";
import axios from "axios";
import WaterApiList from "../../../Components/ApiList/WaterApiList";
import { useNavigate } from "react-router-dom";
import { ThreeDots } from "react-loader-spinner";
import { string } from "yup";
import Select from 'react-select';
import { contextVar } from "../../../Components/ContextVar";


const customStyles = {
  overlay: {
    // background: "rgba(0, 0, 0, 0.5)",
    background: "#e6e6fc",
    // overflowY: "scroll"
  },
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    backgroundColor: "transparent",
    border: "none",
    height: "600px", //or maxHeight
  },
};

function WaterScteen0Ulb(props) {
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [ulbList, setUlbList] = useState();
  const [selectedOption, setSelectedOption] = useState(null);

  const { api_ulbList } = WaterApiList();
  const { notify } = useContext(contextVar)


  const navigate = useNavigate();

  useEffect(() => {
    if (props.openUlbModal == 0) setIsOpen(true);
  }, [props.openUlbModal]);

  useEffect(() => {
    axios
      .get(api_ulbList)
      .then(
        (res) => (console.log("ULB LIsts...", res), setUlbList(res.data.data))
      )
      .catch((err) => console.log("Error while fetching ulblist", err));
  }, []);

  function afterOpenModal() { }

  function closeModal() {
    setIsOpen(false);
    navigate(-1);
  }

  const payNow = () => {
    if (!selectedOption.id) {
      return setSelectedOption(null);
    } else {
      // setValidULB(true);
      notify("ULB Selected", "success")
      console.log("Start Button Clicked..");
      props.nextScreen();
      props.setSelectedUlb(selectedOption);
      setIsOpen(false);
    }
  };

  console.log("selectedOption", selectedOption);

  return (
    <div className="">
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <div className="container">
          <div class="relative w-full h-full max-w-3xl md:h-auto border-indigo-600 shadow-indigo-200 shadow-sm rounded">
            <div class="relative bg-white rounded-lg shadow ">
              <div class="flex items-start justify-between p-4 border-b rounded-t darks:border-gray-600">
                <h3 class="text-base md:text-xl font-semibold text-gray-900 darks:text-white"> Applying For Water Connection </h3>
                <button
                  onClick={closeModal}
                  class="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center darks:hover:bg-gray-600 darks:hover:text-white"
                  data-modal-hide="defaultModal"
                >
                  <svg
                    aria-hidden="true"
                    class="w-5 h-5"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                      clip-rule="evenodd"
                    ></path>
                  </svg>
                  <span class="sr-only">Close modal</span>
                </button>
              </div>
              <div class="p-6 space-y-5">
                <p class="md:text-sm leading-relaxed text-gray-500 text-xs">
                  Welcome to the new water connection application form under the Water User Charges Management System. ULBs committed to providing you with safe and reliable water services. This form is designed to make it easy for citizen to apply for a new water connection..
                </p>
                <p class="md:text-sm leading-relaxed text-gray-500 text-xs">
                  To process your application, we require certain documents, such as proof of ownership or tenancy, and information about the property where you want the water connection and documentation from all applicants. Please ensure that you provide accurate and complete information to avoid any delays in the application process.
                </p>
                <p class="md:text-sm leading-relaxed text-gray-500 text-xs">
                  Please read the instructions carefully and provide accurate and complete information to avoid delays in the assessment process. If you have any questions or need assistance, please contact your ULB or citizen helpline number.
                </p>
              </div>



              <div className="block md:flex justify-center space-x-4">
                <div className="font-semibold text-center">Please Select Your ULB</div>

                <div className="md:w-1/2 w-auto mr-5">
                  <Select
                    defaultValue={selectedOption}
                    onChange={setSelectedOption}
                    getOptionLabel={option => option.ulb_name}
                    getOptionValue={option => option.id}
                    options={ulbList}
                    isClearable={true}
                    isLoading={!ulbList}
                  />
                </div>
              </div>

              <div className="text-center">
                {selectedOption && selectedOption?.id != "" ? (
                  <p>
                    You are applying for
                    <span className="text-indigo-500 mx-2">
                      {selectedOption?.ulb_name}
                    </span>{" "}
                  </p>
                ) : (
                  <p className="text-red-500 py-2 mx-2"> Please Select Your ULB</p>
                )}
              </div>

              <div class="flex flex-row-reverse items-center p-6 border-t border-gray-200 rounded-b">
                <button
                  onClick={payNow}
                  disabled={!selectedOption?.id}
                  class="mx-2 text-white disabled:bg-gray-500 disabled:scale-100 bg-indigo-700 hover:bg-indigo-800 focus:ring-4 focus:outline-none focus:ring-indigo-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center darks:bg-indigo-600 darks:hover:bg-indigo-700 darks:focus:ring-indigo-800"
                >
                  START
                </button>
                <button
                  onClick={closeModal}
                  class="mx-2 text-white bg-red-700 hover:bg-red-800 focus:ring-4 focus:outline-none focus:ring-red-300 rounded-lg border border-red-200 text-sm font-medium px-5 py-2.5 focus:z-10 "
                >
                  CANCEL
                </button>
              </div>
            </div>
          </div>
        </div>
      </Modal>
    </div>
  );
}

// ReactDOM.render(<App />, appElement);

export default WaterScteen0Ulb;
