import axios from 'axios'
import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import WaterApiList from '../../../Components/ApiList/WaterApiList'
import CitizenApplyApiList from '../../../Components/CitizenApplyApiList'
import AxiosInterceptors from '../../../Components/GlobalData/AxiosInterceptors'

function WaterAfterSubmit(props) {

    const { api_getProfileDetails } = CitizenApplyApiList()
    const { header } = WaterApiList();

    const navigate = useNavigate()

    console.log("props in waterAfterSubmit", props)


    // Fething logedin user phone no

    useEffect(() => {
        console.log("== 1 == UseEffect Called")
        AxiosInterceptors.get(`${api_getProfileDetails}`, header)
            .then(function (response) {
                console.log('getdata for my profile ....', response.data.data)
                if (response.data.status) {
                    console.log("== 2 == After Getting user data and status true")
                    if (props?.data?.applicationNo) {
                        sendWhatsAppMessage(response.data.data.mobile, props?.data?.applicationNo)
                    }
                }
            })
            .catch(function (error) {
                console.log('getdata for my profile errorrr.... ', error);
            })
    }, [props])


    ////////// ==> WhatsApp Start


    const sendWhatsAppMessage = (mobile, AppNo) => {
        console.log("== 3 == Whatsapp function Called", mobile, AppNo)
        // const mobile = mobile;
        const module = "Water";

        const accessToken = process.env.REACT_APP_WP_ACCESS_TOKEN;
        const phoneNoId = '103215889230412';

        // console.log("Values", number, type, parameter)
        const mobileNo = +91 + mobile;
        const config = {
            headers: { Authorization: `Bearer ${accessToken}` }
        };

        const bodyParameterSimple = {  // This is for send message directly without variables

            messaging_product: "whatsapp",
            to: mobileNo,
            "type": "template",
            "template": {
                "name": "judco_service_application_reg",
                "language": {
                    "code": "en_US",
                    "policy": "deterministic"
                },
                "components": [
                    {
                        "type": "body",
                        "parameters": [
                            {
                                "type": "text",
                                "text": module
                            },
                            {
                                "type": "text",
                                "text": AppNo
                            }
                        ]
                    },


                ]
            }
        };

        AxiosInterceptors.post(`https://graph.facebook.com/v14.0/${phoneNoId}/messages`, bodyParameterSimple, config)
            .then((res) => console.log("Water WhatsApp message Sent Succfully", res))
            .catch((err) => console.log("Error While Water Sendign WhatsApp Message", err));

    }



    ////////// ==> WhatsApp END


    return (
        <>
            <h1 className='mt-8 mb-2 font-serif font-semibold text-gray-600'></h1>
            <div className="block p-4 md:py-6 shadow-lg bg-indigo-100 border border-indigo-300 rounded  mx-auto ">
                <div className='text-center space-y-4'>
                    <p className='font-semibold'>Your Application has been Submitted Successfully.</p>
                    <p><span className='text-sm'> Application No </span><span className='text-indigo-600 font-semibold'>{props?.data?.applicationNo}</span></p>
                    <button onClick={() => navigate('/water-dashboard')} className='bg-white hover:bg-indigo-600 hover:text-white text-indigo-700  border border-indigo-600 mx-2 rounded-sm text-sm px-4 py-2 shadow-xl'>Dashboard</button>
                    <button onClick={() => navigate(`/water-doc-upload/${props?.data?.applicationId}`)} className='bg-indigo-600 hover:bg-indigo-700 text-white mx-2 rounded-sm text-sm px-4 py-2 shadow-xl'>Upload Document</button>
                </div>
            </div>
        </>
    )
}

export default WaterAfterSubmit