import React, { useState } from 'react'
import { AccordionPage } from './ApplyFromScreens/AccordionPage';
import WaterAfterSubmit from './ApplyFromScreens/WaterAfterSubmit';
import WaterFormScreen1 from './ApplyFromScreens/WaterFormScreen1';
import WaterScteen0Ulb from './ApplyFromScreens/WaterScteen0Ulb';

function WaterApply() {

  const [currentScreenNo, setCurrentScreenNo] = useState(0)
  const [waterApplicationNo, setWaterApplicationNo] = useState()
  const [selectedUlb, setSelectedUlb] = useState()

  const screen1Data = (data) => {
    console.log("Screen 1 Data After Apply Water", data)
    setWaterApplicationNo(data)
    setCurrentScreenNo(2)
  }



  return (
    <>

      <WaterScteen0Ulb openUlbModal={currentScreenNo} nextScreen={() => setCurrentScreenNo(1)} setSelectedUlb={(e) => setSelectedUlb(e)} />
      <div className="grid grid-cols-12 gap-10">

        <div className="md:col-span-9 col-span-12">
          <p className="border p-2 bg-indigo-500 text-gray-200 rounded shadow-md">You are applying <span className='font-semibold'> Water Connection </span> for <span className='font-semibold text-gray-100'>{selectedUlb?.ulb_name}</span></p>
          <div className={`${currentScreenNo == 1 ? 'block' : 'hidden'} `}>   <WaterFormScreen1 screen1Data={screen1Data} selectedUlbId={selectedUlb?.id} /></div>
          <div className={`${currentScreenNo == 2 ? 'block' : 'hidden'} `}>   <WaterAfterSubmit data={waterApplicationNo} /></div>
        </div>


        <div className="md:col-span-3 col-span-12">
          <p className="border-2 border-indigo-400 p-2 text-center bg-white text-xl shadow-md"> Frequently Asked Questions </p>
          <AccordionPage />
        </div>
      </div>

    </>
  )
}

export default WaterApply